import React from 'react';
import styled from 'styled-components';
import bgLogin from '../images/login-background.jpg'
import ctaOne from '../images/cta-logo-one.svg'
import ctaTwo from '../images/cta-logo-two.png'

function Login() {
    return (
        <Container>
            <CTA>
                <CTALogoOne src={ctaOne} />
                <SignUp>GET ALL THERE</SignUp>
                <Description>Lorem ipsum dolor sit amet consectetur adipisicing elit. Exercitationem dignissimos eum voluptatem? Molestias in illo vitae modi a accusamus non, labore ipsam eos praesentium facilis expedita ipsum doloribus similique dolorum.</Description>
                <CTALogoTwo src={ctaTwo} />
            </CTA>
        </Container>
    )
}

export default Login;

const Container = styled.div`
    position: relative;
    height: calc(100vh - 70px);
    display: flex;
    align-items: top;
    justify-content: center;
    

    &:before{
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-image: url(${bgLogin});
        background-position: top;
        background-size: cover;
        background-repeat: no-repeat;
        z-index: -1;
        opacity: 0.7;
    }
`;

const CTA = styled.div`
    max-width: 650px;
    padding: 80px 40px;
    width: 90%;
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    align-items: center;
`;

const CTALogoOne = styled.img`

`;

const SignUp = styled.a`
    width: 100%;
    background-color: #0063e5;
    font-weight: bold;
    padding: 17px 0px;
    border-radius: 4px;
    color: #f9f9f9;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    transition: all 250ms;
    letter-spacing: 1.5px;
    margin-top: 8px;
    margin-bottom: 12px;

    &:hover{
        background-color: #0483ee; 
    }

`;

const Description = styled.p`
    font-size: 11px;
    letter-spacing: 1.5px;
    text-align: center;
    line-height: 1.5;
`;

const CTALogoTwo = styled.img`
    width: 90%;
`;